<template>
  <div class="seepbox">
      <!-- 标签页 -->
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
            <el-tab-pane label="项目信息" name="first">
                <!-- 信息表格渲染 -->
              <!-- <button @click="Fnviewproject">获取样式表</button> -->
              <el-row :gutter="20">
                <el-col :span="8">项目名称</el-col>
                <el-col :span="16">{{ruleForm.p_name}}</el-col>
              </el-row>
              <el-row :gutter="20">
                <el-col :span="8">省</el-col>
                <el-col :span="16">{{ruleForm.p_province}}</el-col>
              </el-row>
              <el-row :gutter="20">
                <el-col :span="8">市</el-col>
                <el-col :span="16">{{ruleForm.p_city}}</el-col>
              </el-row>
              <el-row :gutter="20">
                <el-col :span="8">甲方单位</el-col>
                <el-col :span="16">{{ruleForm.p_party_a}}</el-col>
              </el-row>
              <el-row :gutter="20">
                <el-col :span="8">采购日期</el-col>
                <el-col :span="16">{{ruleForm.p_purchase_time}}</el-col>
              </el-row>
              <el-row :gutter="20">
                <el-col :span="8">竣工日期</el-col>
                <el-col :span="16">{{ruleForm.p_built_time}}</el-col>
              </el-row>
              <el-row :gutter="20">
                <el-col :span="8">联系人姓名</el-col>
                <el-col :span="16">{{ruleForm.p_contact_name}}</el-col>
              </el-row>
              <el-row :gutter="20">
                <el-col :span="8">联系人的电话</el-col>
                <el-col :span="16">{{ruleForm.p_contact_number}}</el-col>
              </el-row>
              <el-row :gutter="20">
                <el-col :span="8">客户类型</el-col>
                <el-col :span="16">{{ruleForm.p_customer_type}}</el-col>
              </el-row>
              <el-row :gutter="20">
                <el-col :span="8">项目类型</el-col>
                <el-col :span="16">{{ruleForm.p_type}}</el-col>
              </el-row>
              <el-row :gutter="20">
                <el-col :span="8">地址</el-col>
                <el-col :span="16">{{ruleForm.p_address}}</el-col>
              </el-row>
              <el-row :gutter="20">
                <el-col :span="8">安装时间</el-col>
                <el-col :span="16">{{ruleForm.p_setup_time}}</el-col>
              </el-row>
              <el-row :gutter="20">
                <el-col :span="8">项目简介</el-col>
                <el-col :span="16">{{ruleForm.p_description}}</el-col>
              </el-row>
            </el-tab-pane>
            <el-tab-pane label="画面" name="second">
              <iframe :src="ruleForm.p_dashboard" frameborder="0" scrolling="no" width="1200" height="750"></iframe>
            </el-tab-pane>
            <el-tab-pane label="采集设备" name="fourth">
              <table class="table" cellspacing="0" cellpadding="0">
                <tbody v-if="ISfromBox">
                    <tr><td>设备名称</td><td>{{formLabelAlign.cd_name}}</td></tr>
                    <tr><td>出场日期</td><td>{{formLabelAlign.cd_produce_time}}</td></tr>
                    <tr><td>设备生产商</td><td>{{formLabelAlign.cd_manufacturer}}</td></tr>
                </tbody>
              </table>
              <div class="tipfromBox" v-if="!ISfromBox">
                暂无数据
              </div>
            </el-tab-pane>
            <el-tab-pane label="关联的员工" name="shezhi">
              <router-view/>
            </el-tab-pane>
        </el-tabs>
  </div>
</template>

<script>
import url from "../../common/api";
import { mapState } from "vuex";
  export default {
    data() {
      return {
        activeName: 'first',
        formInline: {//数据4--查询变量
          user: ''
        },
        value:false,//数据4-自动更新
        StableData: [{//数据4-表格展示内容
            date: '2016-05-03',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
          }, {
            date: '2016-05-02',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
          }, {
            date: '2016-05-04',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
          }, {
            date: '2016-05-07',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
        }],
        ruleForm: {//表单提交信息
            "p_name": "",
            "p_description": "",
            "p_type": "",
            "p_customer_type": "",
            "p_party_a": "",
            "p_purchase_time": "",
            "p_setup_time": "",
            "p_built_time": "",
            "p_contact_name": "",
            "p_contact_number": "",
            "p_province": "",
            "p_city": "",
            "p_address": "",
 
        },
        rules: {//表单信息验证
          p_name: [
            { required: true, message: '请输入项目名称', trigger: 'blur' }
          ],
          p_type: [
            { required: true, message: '请输入项目类型', trigger: 'blur' }
          ],
          p_party_a: [
            { required: true, message: '请输入甲方单位', trigger: 'blur' }
          ],
          p_province: [
            { required: true, message: '请输入省份', trigger: 'blur' }
          ],
          p_city: [
            { required: true, message: '请输入城市', trigger: 'blur' }
          ],
          p_address: [
            { required: true, message: '请输入地址', trigger: 'blur' }
          ],
        },
        formLabelAlign:{},
        ISfromBox:true
      };
    },
    methods: {
      handleClick(tab, event) {//标签页
        // console.log(tab.label,event);
        if(tab.label=='关联的员工'){
          console.log('--------dianji关联的员工')
          this.$router.push({
              path:'/index/MFseeProjectManagement/MFsignproject', //也可以在路径上拼接
              query:{
                  company_id:this.$route.query.company_id,      //传递的值
                  project_id:this.$route.query.project_id
              }
          })
        }else{
        }
      },
      onSubmit(){//数据4--表单查询事件
        console.log('查询表单提交事件')
      },
      Fnhistory(i){//数据4--列表中操作按钮-历史数据
        console.log('列表中操作按钮-历史数据')
      },
      Fnviewproject(){//查看项目信息
        url.viewproject({
          "project_id": this.$route.query.project_id,
          "company_id": this.$route.query.company_id,
          "user_id": this.userInfo.user_id
        }).then(res=>{
          console.log(res,'------查看项目信息')
          if(res.data.code[0]='200'){
            let xx=res.data.data[0].fields
            xx.project_id=res.data.data[0].pk
            this.ruleForm=xx
          }else{
            this.$message.error('查看项目信息失败')
          }
        })
      },
      Fncollectdevicelist(){
        url.collectdevicelist({
          "project_id": this.$route.query.project_id,
          "user_id": this.userInfo.user_id
        }).then(res=>{
          console.log(res,'-----------列表')
          if(res.data.data.length==0){
            this.ISfromBox=false;
          }else{
            let xx=res.data.data[0].fields
            xx.cdevice_id=res.data.data[0].pk
            this.formLabelAlign=xx
          }

        })
      },
    },
    computed:{
      ...mapState(['userInfo'])
    },
    mounted(){
      this.Fnviewproject()//获取项目信息
      this.Fncollectdevicelist()//数据
    }
  };
</script>

<style lang="stylus" scoped>
  .seepbox
    margin:25px 30px
    min-width: 1000px
    >>>.el-row
      margin 15px
    >>>.el-col-8
      text-align right
      color #666
  .text {
    font-size: 16px;
  }

  .item {
    margin-bottom: 18px;
    
  }
  .textItemRight
    background #f00
    font-size 14px
    height 13px
    padding 5px
    line-height 13px
    text-align center
    color #fff
    border-radius 50%
    float right
  .textItemRight_bgAll
    background #36c6d3
  .textItemRight_bg1
    background #ed6b75
  .textItemRight_bg2
    background #CC6600
  .textItemRight_bg3
    background #CC9933
  .textItemRight_bg4
    background #F1C40F
  .textItemRight_bg5
    background #659be0
.shezhiL
  margin-top30px
  div
    line-height 40px
    padding-left 20px
    margin 5px 0
  .shezhiLactive
    background #f1f1f1
    border-left 4px solid #409EFF
.box-card
  margin-bottom 25px
  >>>.el-checkbox
    display block
.table {
  border: 1px solid #ddd;
  border-collapse: collapse;
}

.table thead tr th,
.table tbody tr td {
  padding: 8px 60px;
  text-align: center;
  color: #333;
  border: 1px solid #ddd;
  border-collapse: collapse;
  background-color: #fff;
}
.tipfromBox
  margin 100px 0
  text-align center
  color #999
  font-size 18px
</style>


